@import url('https://fonts.googleapis.com/css2?family=Monteserrat&display=swap');
*{
  box-sizing: border-box;
  margin:0;
  padding:0;
  font-family: 'Monteserrat', sans-serif;
  background-color: #36454F;
  color: #fff;

}

.coin-app{
  display:flex;
  flex-direction: column;
  align-items:center;
  margin-top: 64px;
  color: #fff;
}

.coin-search{
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.coin-text{
  margin-bottom:32px;
  text-align:center;
}

.coin-input{
  padding-left:16px;
  width:300px;
  height:50px;
  border-radius:4px;
  border:none;
  background-image: linear-gradient(-225deg, #000000 0%, #bdbab7 68%);
}

.coin-input::placeholder{
  color:#e2e2e2;
}
